$grey-bg: #f2f2f2;

.footer {
  background-color: $grey-bg;
  max-width: 1440px;
  margin: 0 auto;
}

.footer__wrap {
  padding: 0 20px;
}

.footer__logo {
  padding-top: 40px;
  padding-bottom: 32px;
  text-align: center;
  &:hover {
    opacity: 0.8;
  }
  &:active {
    opacity: 0.6;
  }
}

.footer__socials {
  padding: 20px 77px 21px 73px;
  text-align: center;
  text-decoration: none;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-top: #d9d9d9 1px solid;
  border-bottom: #d9d9d9 1px solid;
}

.socials__link:not(:last-child) svg,
.copy__link svg {
  fill: #666666;
}

.socials__link:not(:last-child):hover svg,
.copy__link:hover svg {
  fill: #68b738;
  opacity: 0.8;
}

.socials__link:last-child svg {
  stroke: #666666;
}
.socials__link:hover:last-child svg {
  stroke: #68b738;
  opacity: 0.8;
}

.socials__link:active svg,
.copy__link:active svg {
  opacity: 0.6;
}

.footer__copy {
  padding-top: 22px;
  padding-bottom: 22px;
}

.copy__link {
  display: flex;
  justify-content: space-between;
  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-decoration: none;
  &:hover {
    opacity: 0.6;
  }
}
